.App{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #252a34;
}
main{
  background-color: #eaeaea;
  height: 90vh;
  width: 40vw;
  padding-right: 3vw;
  padding-left: 3vw;
  border-radius: 15px 15px 15px 15px;
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.4)
}
.notas-container{
  height: 35%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 18px;
}
.calculos-container{
  height: 50%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 18px;
  grid-template-rows: 1fr 1fr;
  row-gap: 18px;
}
.title{
  font-weight: 600;
  font-size: 2rem;
}
.calculos-container .text-secondary{
  font-size: 1rem;
  font-weight: 400;
}
h2{
  text-align: center;
  color:  #252a34
}
@media (max-width: 1400px) 
{
  main{
    width: 65vw;
  }
}
@media (max-width: 1200px) 
{
  main{
    width: 75vw;
  }
}
@media (max-width: 1000px) 
{
  main{
    width: 100vw;
    height: 100vh;
    border-radius: 0 0 0 0;
  }
}
@media (max-height: 800px) 
{
  .App, main{
    height: 100%;
  }
  .calculos-container{
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
}